@font-face {
  font-family: playerFont;
  src: 
  	url('URL_BASE/tv_sans.woff') format('woff'),
  	url('URL_BASE/tv_sans.ttf') format('truetype');
}

.playerMainShell {
  z-index: 10001;
  font-family: playerFont;
  font-size: 15px;
  color: white;
  -webkit-touch-callout: none;/* iOS Safari */
  -webkit-user-select: none;/* Chrome/Safari/Opera */
  -moz-user-select: none;/* Firefox */
  -ms-user-select: none;/* Internet Explorer/Edge */
  user-select: none;
  cursor: default;
  position: relative;
  width: 100%;
  height: 100%;
  background: transparent;
  border: 0;
  margin: 0;
  padding: 0;
  line-height: 1;
  box-sizing: border-box;
}

.playerMainShell svg {
	pointer-events: none;
}

.playerMainShell,
.playerMainShell * {
  outline: 0;
}

.playerMainShell.loadingMainScreenActive #loadingOverlay,
.playerMainShell.videoMainScreenActive #video,
.playerMainShell.videoMainScreenActive #overlay,
.playerMainShell.errorMainScreenActive #errorOverlay,
.playerMainShell.nextInSeriesMainScreenActive #nextInSeriesOverlay {
  display: block;
}
.playerMainShell.allControlsHidden.videoMainScreenActive #overlay {
  display: none;
}
.playerMainShell.replayScreenActive.videoMainScreenActive #video {
  visibility: hidden;
}

.unskinnedPlayer .playerMainShell.loadingMainScreenActive #loadingOverlay,
.unskinnedPlayer .playerMainShell.videoMainScreenActive #overlay,
.unskinnedPlayer .playerMainShell.errorMainScreenActive #errorOverlay,
.unskinnedPlayer .playerMainShell.nextInSeriesMainScreenActive #nextInSeriesOverlay {
