body.iOSfullscreen {
	width: 100%;
	height: 100%;
	overflow: hidden !important;
	position: fixed;
}

body.iOSfullscreen .playerMainShell {
	position: fixed;
	left: 0;
	top: 0;
  width: 100%;
  height: 100%;
}

.playerMainShell.hbbTvActive #fullScreenShell,
.playerMainShell.hbbTvActive #subtitlesShell {
	display: none;
}
.playerMainShell #settingsDiv {
  display: none;
  position: absolute;
  text-align: center;
  white-space: nowrap;
  background: rgba(14,17,29,0.859);
  border-radius: 4px;
  z-index: 2;
}
.playerMainShell.settingsScreenActive #settingsDiv {
	display: block;
}
.playerMainShell #settingsDiv.innerPageActive {
	text-align: left;
}
.playerMainShell.settingsLayout1 .innerPageActive .settingsColumn,
.playerMainShell.settingsLayout2 .innerPageActive .settingsColumn {
	visibility: hidden; /* nechame sloupce na miste, aby settingsDiv mel minimalne puvodni rozmer; innerPage je prekryje */
}
.playerMainShell.settingsLayout3 .innerPageActive .settingsColumn,
.playerMainShell.settingsLayout4 .innerPageActive .settingsColumn {
	display: none; /* sloupce skryjeme, innerPage bude novy sloupec */
}
.playerMainShell #settingsInnerPage {
  display: none;
  width: 100%;
  height: 100%;
}
.playerMainShell.settingsLayout1 #settingsInnerPage,
.playerMainShell.settingsLayout2 #settingsInnerPage {
  position: absolute;
  top: 0;
  left: 0;
}
.playerMainShell.settingsLayout1 .innerPageActive #settingsInnerPage,
.playerMainShell.settingsLayout2 .innerPageActive #settingsInnerPage {
	display: block;
}
.playerMainShell.settingsLayout3 .innerPageActive #settingsInnerPage,
.playerMainShell.settingsLayout4 .innerPageActive #settingsInnerPage {
	display: table-cell;
	vertical-align: middle;
	min-height: 100%;
	position: relative;
}
.playerMainShell #settingsInnerPageContents {
  	padding: 26px;
  	box-sizing: border-box;
}
.playerMainShell.settingsLayout1 .innerPageActive #settingsInnerPageContents,
.playerMainShell.settingsLayout2 .innerPageActive #settingsInnerPageContents {
	position: absolute;
	left: 0;
	width: 100%;
	top: 50%;
	transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
}
.playerMainShell.settingsLayout3 .innerPageActive #settingsInnerPageContents,
.playerMainShell.settingsLayout4 .innerPageActive #settingsInnerPageContents {
	display: inline-block;
	width: 100%;
}
.playerMainShell.settingsLayout4 .innerPageActive #settingsInnerPageContents {
  	padding: 14px 20px 14px 14px;
}
.playerMainShell #settingsInnerPage:before {
  content: '';
  background: rgba(14,17,29,0.961);
  position: absolute;
}
.playerMainShell.settingsLayout1 #settingsInnerPage:before,
.playerMainShell.settingsLayout2 #settingsInnerPage:before,
.playerMainShell.settingsLayout3 #settingsInnerPage:before {
  top: 7px;
  left: 7px;
  right: 7px;
  bottom: 7px;
  border-radius: 2px;
}
.playerMainShell.settingsLayout4 #settingsInnerPage:before {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.playerMainShell .addTvActive #addTvDiv {
	display: block;
	width: 100%;
}
.playerMainShell #addTvDiv {
  position: relative;
  display: none;
  text-align: left;
  white-space: normal;
  font-size: 14px;
}
.playerMainShell .addTvLayout3 #addTvDiv,
.playerMainShell .addTvLayout4 #addTvDiv,
.playerMainShell .addTvLayout5 #addTvDiv,
