	display: block;
}
.playerMainShell #audioShell .subtitlesLine span {
  background: transparent;
  padding: 0 25px;
}
.playerMainShell #audioShell .subtitlesLine:first-child span {
  padding-top: 12px;
  padding-bottom: 7px;
}
.playerMainShell #audioSubtitlesShell.empty {
  display: none;
}
.playerMainShell #audioSubtitlesShell #settingsSubtitles .listItem {
  height: 36px;
  line-height: 36px;
  background: rgba(255,255,255,0.302);
  color: white;
