  color: rgba(23, 26, 38,1);
  width: 100%;
  height: 66px;
  line-height: 1;
}
.playerMainShell #audioShell #subtitlesShell.hasSubtitles {
	display: block;
}

.playerMainShell #versionInfoShell {
